import React from "react";
import {useParams} from 'react-router-dom'
function CatTpBanner(props){
    return (
        <div className="px-2 xl:px-0 ">
            <div className="w-full capitalize h-fit py-8 px-4 flex justify-center items-center rounded mt-4 mb-1 bg-gray-700 text-white text-2xl">
                {useParams().ParentCat} Category
            </div>
        </div>
    )
}

export default CatTpBanner;