import React, { useContext, useState, useEffect} from "react";
import CatTpBanner from "./Cat/CatTopBanner";
import {useParams, Link} from 'react-router-dom'
import ProductContext from "./Context/ProductContext";
import CardCat from "./Cat/CardCat";
import ScrollTopAction from "./Action/ScrollTopAction";

function CatPage(props){  
  let [color, changeColor] = useState('none');
  let [load, setload] = useState(false);
  let [firstload, setfirstload] = useState(false);
  let data = useParams();
  let word = '';
  let [count, setcount] = useState(6);
  useEffect(()=>{
    document.title = "HM Shop | Category";
    setcount(6);
    setfirstload(true);
    setTimeout(()=>{
      setfirstload(false);
    }, 1000);
  }, [data.ParentCat,data.ChildCat]);
  if(data.ParentCat.toLowerCase() == 'women'){
    word = 'women'
  }else if(data.ParentCat.toLowerCase() == 'men'){
    word = 'men';
  }
  
  let setColor = (e)=>{
    changeColor(e.target.value);
    ScrollTopAction();
  }

    let {state, action} = useContext(ProductContext);
    let more = ()=>{
      setload(true);
      setTimeout(()=>{
        setcount(count + 6);
        setload(false);
      }, 1000);
    }

    let price = [];
    state.product.map(item=>price.push(item.price));
    let [max, setMax] = useState(Math.max(...price));
    let changeprice = (e)=>{
      setMax(e.target.value);
    }
    
    let product = state.product.filter(item=>item.parentCat == word).filter(item=>
      data.ChildCat ? 
      item.childCat == data.ChildCat
      :
      true
      ).filter(item=> {
      if(color == 'none'){
        return item;
      }else{
        return item.color.indexOf(color) >= 0
      }
    } ).filter(item=> {
      if(item.price <= max){
        return item;
      }
    } );
    
    let ProductCart = product.slice(0, count).map((item, key)=><CardCat img={item.img} price={item.price} title={item.title} id={item.id} key={key} />);
    let WomenCat = state.cat.women.map((item, key)=><Link key={key} onClick={ScrollTopAction} to={`/cat/women/${item}`} className="text-center w-full border-b-[1px] py-4 px-4 border-white hover:border-b-[1px] hover:border-gray-600 transition-all duration-200">{item}</Link>);
    let MenCat = state.cat.men.map((item, key)=><Link key={key} onClick={ScrollTopAction} to={`/cat/men/${item}`} className="text-center w-full border-b-[1px] py-4 px-4 border-white hover:border-b-[1px] hover:border-gray-600 transition-all duration-200">{item}</Link>);

    return (
        <div className="container mx-auto px-2 xl:px-0 flex flex-col">
          <CatTpBanner />
          <div className="flex flex-col lg:flex-row justify-around">
            {/* productCat */}
          <div className="flex flex-col items-center w-full lg:w-3/4 h-fit">
            {ProductCart.length ?
            <div className={`flex-wrap flex-col md:flex-row justify-center px-2  xl:px-0 md:justify-between w-full ${firstload ? 'hidden' : 'flex'}`}>
            {ProductCart}
            </div>
            :
            <div className={`text-center block w-full text-gray-500 text-2xl py-12 ${firstload ? 'hidden' : 'block'}`}>
            No results found
            </div>
            }
            <div>
            <span className={`material-icons animate-spin text-3xl my-12 ${!firstload ? 'hidden' : 'block'}`}>
            sync
            </span>
            <span className={`material-icons animate-spin text-3xl mt-2 ${load ? "block" : "hidden"}`}>
            sync
            </span>
            </div>
            <div className="px-2 w-full flex xl:px-0">
            <div onClick={more} className={`bg-gray-700  hover:bg-gray-800 cursor-pointer w-full text-white text-center p-4 rounded my-4 transition-all duration-200 ${product.length <= count ? 'hidden' : ''} ${firstload ? 'hidden' : 'block'}`}>
                Load More
            </div>
            </div>
          </div>
          {/* end ProductCat */}

          {/* SidebarCat */}
          <div className="w-full lg:w-1/4 h-fit py-3 flex flex-col px-2 xl:px-0 xl:pl-4">
        <div className="flex flex-col items-center border-[1px] border-gray-300 p-4 shadow rounded mb-4">
            <Link onClick={ScrollTopAction} to={`/cat/women`} className="text-xl py-2 w-full text-center bg-gray-700 rounded text-white">Women</Link>
            <ul className="flex flex-col items-center">
                    {WomenCat}
            </ul>
        </div>
        <div className="flex flex-col items-center border-[1px] border-gray-300 p-4 shadow rounded mb-4">
            <Link onClick={ScrollTopAction} to={`/cat/men`} className="text-xl py-2 w-full text-center bg-gray-700 rounded text-white">Men</Link>
            <ul className="flex flex-col items-center">
            {MenCat}
            </ul>
        </div>
        <div className="flex flex-col items-center border-[1px] border-gray-300 p-4 shadow rounded mb-4">
            <h1 className="text-xl py-2 w-full text-center bg-gray-700 rounded text-white flex flex-col items-center pt-5">Filter
            <span className="material-icons text-white p-1 ">
                expand_more
                </span>
            </h1>
            <div className="w-full h-fit my-2 flex justify-center flex-wrap">
            <label htmlFor="none">
                <input onClick={setColor} type="radio" name="color" id="none"  value="none"  className="hidden" />
                <div className="w-[50px] h-[50px] rounded-full bg-white border-4 border-gray-400 m-2 flex justify-center items-center">All</div>
            </label>
            <label htmlFor="gray">
                <input onClick={setColor} type="radio" name="color" id="gray" value="gray" className="hidden" />
                <div className="w-[50px] h-[50px] rounded-full bg-[gray] border-4 border-white m-2"></div>
            </label>
            <label htmlFor="#3b5490">
                <input onClick={setColor} type="radio" name="color" id="#3b5490" value="#3b5490" className="hidden" />
                <div className="w-[50px] h-[50px] rounded-full bg-[#3b5490] border-4 border-white m-2"></div>
            </label>
            <label htmlFor="#374151">
                <input onClick={setColor} type="radio" name="color" id="#374151"  value="#374151"  className="hidden"/>
                <div className="w-[50px] h-[50px] rounded-full bg-[#374151] border-4 border-white m-2"></div>
            </label>
            <label htmlFor="#f44c4c">
                <input onClick={setColor} type="radio" name="color" id="#f44c4c"  value="#f44c4c"  className="hidden"/>
                <div className="w-[50px] h-[50px] rounded-full bg-[#f44c4c] border-4 border-white m-2"></div>
            </label>
            <label htmlFor="#83b0d9">
                <input onClick={setColor} type="radio" name="color" id="#83b0d9"  value="#83b0d9"  className="hidden"/>
                <div className="w-[50px] h-[50px] rounded-full bg-[#83b0d9] border-4 border-white m-2"></div>
            </label>
            <label htmlFor="#bf7f73">
                <input onClick={setColor} type="radio" name="color" id="#bf7f73"  value="#bf7f73"  className="hidden"/>
                <div className="w-[50px] h-[50px] rounded-full bg-[#bf7f73] border-4 border-white m-2"></div>
            </label>
            <label htmlFor="#ebe1d5">
                <input onClick={setColor} type="radio" name="color" id="#ebe1d5"  value="#ebe1d5"  className="hidden"/>
                <div className="w-[50px] h-[50px] rounded-full bg-[#ebe1d5] border-4 border-white m-2"></div>
            </label>
            <label htmlFor="#646758">
                <input onClick={setColor} type="radio" name="color" id="#646758"  value="#646758"  className="hidden"/>
                <div className="w-[50px] h-[50px] rounded-full bg-[#646758] border-4 border-white m-2"></div>
            </label>
            <label htmlFor="black">
                <input onClick={setColor} type="radio" name="color" id="black"  value="black"  className="hidden"/>
                <div className="w-[50px] h-[50px] rounded-full bg-[black] border-4 border-white m-2"></div>
            </label>
            
            
            </div>
            <div className="flex flex-col items-center w-full">
            <div className="my-4 text-xl">Price: <label htmlFor="price" id="htmlForprice">{max}</label> $</div>
            <input type="range" name="price" id="price" min={Math.min(...price)} max={Math.max(...price)} value={max} onChange={changeprice} className="w-full h-2 bg-blue-100 rounded appearance-none"/>
            </div>
            
        </div>
    </div>        

    {/* end SidebarCat */}
            
          </div>
        </div>
    )
}

export default CatPage;